.calculator {
  width: 250px;
  padding: 20px;
  border-radius: 10px;
  background: #f5f5f5;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.calculator-display {
  width: 100%;
  height: 60px;
  background: #222;
  color: #fff;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-bottom: 20px;
}

.calculator-buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.calculator-buttons button {
  width: 100%;
  height: 50px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background: #e0e0e0;
  cursor: pointer;
  transition: background 0.2s;
}

.calculator-buttons button:hover {
  background: #d4d4d4;
}

.calculator-buttons button:active {
  background: #ccc;
}
